










































import { Component, Mixins, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import BaseIcon from '@/components/base/icon/BaseIcon.vue'
import BaseTable from '@/components/base/table/BaseTable.vue'
import DistrictMixin from '@/mixins/commonMixin'

@Component({ components: { BaseTable, BaseIcon } })
export default class PopupShowError extends Mixins(PopupMixin, DistrictMixin) {
  data: Array<Record<string, any>> = []
  @Prop({ required: true, type: Array, default: [] }) dataError!: Array<Record<string, any>>
  @Prop({ required: true, type: String, default: '' }) fileName!: string

  detail: Record<string, any> = {}

  tableHeight: string | number = 'auto'
  getHeightOfTable(): void {
    this.tableHeight = `${window.innerHeight - 123}px`
  }
  mounted(): void {
    this.getHeightOfTable()
  }

  totalErrorCount = 0

  get getIndex(): number {
    return this.dataError.length * (1 - 1) + 1
  }

  get fileNameExcel(): string {
    if (this.fileName) {
      return this.fileName.replaceAll('.xlsx', ' ')
    }
    return ''
  }
  handleOpen(): void {
    // this.$message.error('Nhập từ file không thành công. Vui lòng kiểm tra chi tiết lỗi bên dưới')
    const h = this.$createElement
    this.$message({
      customClass: 'notify-error',
      type: 'error',
      dangerouslyUseHTMLString: true,
      message: h('div', { class: 'message' }, [h('div', [h('span', { class: 'detail' }, 'Nhập từ file không thành công. Vui lòng kiểm tra chi tiết lỗi bên dưới')])]),
      duration: 4000
    })
  }
}
