














































































import { Component } from 'vue-property-decorator'
import BaseIcon from '@/components/base/icon/BaseIcon.vue'
import ProductRepository from '@/services/repositories/product'
import getRepository from '@/services'
import FilterMixin from '@/mixins/filterAuditContractMixin'

@Component({ components: { BaseIcon } })
export default class FilterProgram extends FilterMixin {
  apiProduct!: ProductRepository
  isClose = false
  checkbox = {
    checkbox1: true,
    checkbox2: false,
    checkbox3: false,
    checkbox4: false
  }
  product = {
    numOfFeeYear: '',
    numOfContractYear: '',
    categoryId: '',
    categoryCode: '',
    categoryName: '',
    productType: '',
    dispStatus: ''
  }
  productApi!: ProductRepository
  params = {}
  form: any = {}
  leaderData: any = {}
  productDetail: any = {}
  listRelationship: Record<string, any> = {}

  created(): void {
    this.form = this.user
    this.apiProduct = getRepository('product', {
      districtId: this.user?.districts?.[0].id
    })
    this.productCategory()
  }

  handleChangeInput(value: string): void {
    let memberList = this.listRelationship.filter((item: any) => item.categoryId == value)
    this.productDetail = memberList[0]
    this.product.categoryId = this.productDetail.categoryId
    this.product.categoryName = this.productDetail.categoryName
    this.product.categoryCode = this.productDetail.categoryCode
  }

  productCategory(): void {
    this.apiProduct.productCategory().then((res: any) => {
      this.listRelationship = res
    })
  }

  handleSearch(): void {
    if (this.checkbox.checkbox1 == true && this.checkbox.checkbox2 == false) {
      this.product.dispStatus = 'ACTIVE'
    } else if (this.checkbox.checkbox1 == false && this.checkbox.checkbox2 == true) {
      this.product.dispStatus = 'INACTIVE'
    } else {
      this.product.dispStatus = ''
    }
    if (this.checkbox.checkbox3 == true && this.checkbox.checkbox4 == false) {
      this.product.productType = 'MAIN'
    } else if (this.checkbox.checkbox3 == false && this.checkbox.checkbox4 == true) {
      this.product.productType = 'SUB'
    } else {
      this.product.productType = ''
    }
    this.apiProduct.getProduct(this.product).then(() => {
      this.$emit('search', this.product)
      this.isClose = false
    })
  }

  handleReset(): void {
    this.checkbox = {
      checkbox1: true,
      checkbox2: false,
      checkbox3: false,
      checkbox4: false
    }
    this.product = {
      numOfFeeYear: '',
      numOfContractYear: '',
      categoryId: '',
      categoryCode: '',
      categoryName: '',
      productType: '',
      dispStatus: ''
    }
  }
}
