
























































































































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import { Product } from '@/services/entities/product'
import ProductRepository from '@/services/repositories/product'
import PopupMixin from '@/mixins/popup'
import FilterMixin from '@/mixins/filterAuditContractMixin'
import getRepository from '@/services'
import { clone } from 'lodash'

@Component({})
export default class CreateProduct extends Mixins(PopupMixin, FilterMixin) {
  @Prop({ required: false, type: Object, default: {} }) ProductEdit!: Product
  @Prop({ required: false, type: String, default: 'add' }) isAction!: string

  closeClick = false
  product: Product = {} as Product
  apiProduct!: ProductRepository
  rules: any = {
    productCode: [{ required: true, message: 'Mã không được để trống', trigger: 'blur' }],
    productName: [{ required: true, message: 'Tên không được để trống', trigger: 'blur' }],
    numOfContractYear: [{ required: true, message: 'Không được để trống', trigger: 'blur' }],
    numOfFeeYear: [{ required: true, message: 'Không được để trống', trigger: 'blur' }],
    familySalesRate: [{ required: true, message: 'Không được để trống', trigger: 'blur' }],
    customerSalesRate: [{ required: true, message: 'Không được để trống ', trigger: 'blur' }],
    familyManagerRate: [{ required: true, message: 'Không được để trống', trigger: 'blur' }],
    customerManagerRate: [{ required: true, message: 'Không được để trống', trigger: 'blur' }]
  }
  leaderData: any = {}
  productDetail: any = {}
  listError: Record<string, boolean> = {
    productType: false,
    categoryId: false
  }
  form: any = {}
  listRelationship: Record<string, any> = {}
  checkError(): void {
    if (this.product.categoryId == null) {
      this.listError.categoryId = true
    } else {
      this.listError.categoryId = false
    }
    if (this.product.productType) {
      this.listError.productType = false
    } else {
      this.listError.productType = true
    }
  }
  validate(): void {
    if (this.product.productType == null) {
      this.listError.productType = false
    }
    if (this.product.categoryId == null) {
      this.listError.categoryId = false
    }
  }
  handleChangeRadio(): void {
    if (this.product.productType) {
      this.listError.productType = false
    } else {
      this.listError.productType = true
    }
  }
  handleChangeInput(id: string): void {
    let memberList = this.listRelationship.filter((item: any) => item.categoryId == id)[0]
    this.product.categoryId = memberList.categoryId
    this.product.categoryName = memberList.categoryName
    this.product.categoryCode = memberList.categoryCode
    if (this.product.categoryId == null) {
      this.listError.categoryId = true
    } else {
      this.listError.categoryId = false
    }
  }
  created(): void {
    this.form = this.user
    this.apiProduct = getRepository('product', {
      districtId: this.user?.districts?.[0].id
    })
  }
  productCategory(): void {
    this.apiProduct.productCategory().then((res: any) => {
      this.listRelationship = res
    })
  }
  validateForm(): boolean {
    let formInput: any = this.$refs.formInput
    let valid = false
    formInput?.validate((_valid: boolean) => {
      valid = _valid
    })
    console.log('22', valid)

    return valid
  }
  handleSave(): void {
    const valid = this.validateForm()
    const _valid = this.listError.productType || this.listError.categoryId
    if (valid && !_valid) {
      const product: Product = this.product

      if (this.isAction === 'add') {
        this.apiProduct
          .createProduct(product)
          .then((res: any) => {
            this.setOpenPopup({
              popupName: 'CreateProduct',
              isOpen: false
            })
            this.$message.success('Thêm dữ liệu thành công!')
            this.$emit('save', res)
            this.$emit('reset')
          })
          .catch((err: any) => {
            this.$message.error(err.message)
          })
      } else {
        if (this.product.productType == null) {
          this.listError.productType = false
        }
        if (this.product.categoryId == null) {
          this.listError.categoryId = false
        }
        if (product.id) {
          const params = {
            productCode: product.productCode,
            productName: product.productName,
            productType: product.productType,
            categoryId: product.categoryId,
            categoryCode: product.categoryCode,
            categoryName: product.categoryName.toString(),
            numOfContractYear: product.numOfContractYear,
            numOfFeeYear: product.numOfFeeYear,
            familySalesRate: product.familySalesRate,
            customerSalesRate: product.customerSalesRate,
            familyManagerRate: product.familyManagerRate,
            customerManagerRate: product.customerManagerRate,
            description: '',
            dispStatus: product.dispStatus
          }
          this.apiProduct
            .updateProduct(this.product.id, params)
            .then((res: any) => {
              this.setOpenPopup({
                popupName: 'CreateProduct',
                isOpen: false
              })
              this.$message.success('Cập nhật dữ liệu thành công!')
              this.$emit('save', res)
              this.$emit('reset')
            })
            .catch((err: any) => {
              this.$message.error(err.message)
            })
        }
      }
    } else {
      this.$message.error('Nhập đầy đủ thông tin bắt buộc!')
      this.checkError()
    }
  }
  getRelationship(): void {
    //
  }
  handleClose(): void {
    //
  }

  handleOpen(): void {
    this.productCategory()
    this.product = { ...this.ProductEdit }
    if (this.isAction === 'add') {
      this.handleReset()
    }
    if (this.isAction === 'edit') {
      this.product.categoryId += ''
      this.listError.productType = false
      this.listError.categoryId = false

      if (this.$refs.formInput) {
        //@ts-ignore
        this.$refs.formInput.clearValidate()
      }
    } else {
      if (this.$refs.formInput) {
        //@ts-ignore
        this.$refs.formInput.clearValidate()
      }
    }
  }
  close(): void {
    this.setOpenPopup({
      popupName: 'CreateProduct',
      isOpen: false
    })
  }
  handleReset(): void {
    if (this.isAction === 'add') {
      this.handleResetNew()
    }
    if (this.isAction === 'edit') {
      //@ts-ignore
      this.$refs.formInput.clearValidate()
      this.handleEdit()
    } else {
      if (this.$refs.formInput) {
        //@ts-ignore
        this.$refs.formInput.clearValidate()
      }
    }
  }
  handleEdit(): void {
    this.product = clone(this.ProductEdit)
    this.product.categoryId += ''
  }
  handleResetNew(): void {
    this.product = {} as Product
    if (this.$refs.formInput) {
      //@ts-ignore
      this.$refs.formInput.clearValidate()
    }
    if (this.product.productType == null) {
      this.listError.productType = false
    }
    if (this.product.categoryId == null) {
      this.listError.categoryId = false
    }
  }
}
