








































































































import { forEach } from 'lodash'
import { Component, Mixins } from 'vue-property-decorator'
import SearchBar from '../components/filters/Searchbar.vue'
import FilterProgram from '../components/filters/Filter.vue'
import PopupMixin from '@/mixins/popup'
import CreateProgram from '../components/popup/CreateProduct.vue'
import getRepository from '@/services'
import ProductRepository from '@/services/repositories/product'
import FilterMixin from '@/mixins/filterAuditContractMixin'
import { PaginationInterface } from '@/interface'
import PopupShowError from '../components/popup/PopupShowError.vue'
import ImportRepository from '@/services/repositories/import/index'

@Component({ components: { SearchBar, FilterProgram, CreateProgram, PopupShowError } })
export default class productPage extends Mixins(PopupMixin, FilterMixin) {
  isLoading = false
  isConflickClick = false
  productApi!: ProductRepository
  apiImport?: ImportRepository
  data: Array<Record<string, any>> = []
  isAction = 'add'
  form: any = {}
  isSpace = false
  dataError = []
  fileName = ''
  query: PaginationInterface = {
    page: 1,
    search: '',
    limit: 20,
    total: 0,
    numOfFeeYear: '',
    numOfContractYear: '',
    categoryId: '',
    categoryCode: '',
    categoryName: '',
    productType: '',
    dispStatus: 'ACTIVE',
    type: 'PRODUCT'
  }
  params: Record<string, any> = {
    type: 'PRODUCT'
  }
  option: Array<Record<string, any>> = [
    {
      id: 1,
      name: 'Mã sản phẩm',
      sortType: 'PRODUCT_CODE',
      active: false,
      divided: false
    },
    {
      id: 2,
      name: 'Tên sản phẩm ',
      sortType: 'PRODUCT_NAME',
      active: false,
      divided: false
    },
    {
      id: 3,
      name: 'Thời hạn hợp đồng',
      sortType: ' NUM_OF_CONTRACT_YEAR',
      active: false,
      divided: false
    },
    {
      id: 4,
      name: 'Thời hạn đóng phí',
      sortType: 'NUM_OF_FEE_YEAR',
      active: false,
      divided: false
    },
    {
      id: 5,
      name: 'Sản phẩm mới nhất',
      sortType: 'CREATED_AT',
      active: false,
      divided: false
    }
  ]
  listRelationship: Record<string, any> = {}
  product: Record<string, any> = {}
  init(): void {
    this.isLoading = true
    const temp = {
      ...this.query
    }
    this.productApi
      .getProduct(temp)
      .then((res: any) => {
        this.data = res.content
        this.query.total = res.totalElements
        this.isLoading = false
      })
      .catch(() => {
        this.isLoading = false
      })
  }
  handleSearchFillter(param: Record<string, any>): void {
    this.query = { ...this.query, ...param }
    this.query.page = 1
    this.init()
  }
  resetSelectContract(): void {
    this.init()
  }
  resetQuery(): void {
    this.query.page = 1
    this.query.limit = 20
  }
  handleSizeChange(size: number): void {
    this.query.limit = size
    this.init()
  }
  handleCurrentChange(page: number): void {
    this.query.page = page
    this.init()
  }
  checkStatus(item: Record<string, any>): string {
    let statusText = ''
    if (item.dispStatus === 'ACTIVE') {
      statusText = 'Hoạt động'
    } else if (item.dispStatus === 'INACTIVE') {
      statusText = 'Không hoạt động'
    }
    return statusText + '</span>'
  }
  handleCreate(): void {
    this.isAction = 'add'
    this.setOpenPopup({
      popupName: 'CreateProduct',
      isOpen: true
    })
  }
  handleClickAction(row: Record<string, any>, action: string): void {
    this.product = row.row
    if (!action) action = 'edit'
    if (action === 'edit') {
      this.isAction = action
      this.setOpenPopup({
        popupName: 'CreateProduct',
        isOpen: true
      })
    }
  }
  handleChangeFile(file: Record<string, any>): void {
    this.uploadFile(file)
  }
  async handleOpenExportFile(): Promise<void> {
    this.isLoading = true
    let response: any

    await this.apiImport
      // eslint-disable-next-line no-undef
      ?.spamleFile(this.districtId, this.params)
      .then(data => {
        response = data.data
        const a = document.createElement('a')
        a.style.display = 'none'
        a.href = response
        document.body.appendChild(a)
        a.click()
        document.body.removeChild(a)
        this.isLoading = false

        this.isLoading = false
      })
      .catch(err => {
        console.log(err)
        this.isLoading = false
      })
  }

  uploadFile(file: Record<string, any>): void {
    const message = this.$message({
      message: 'Đang tải file lên',
      type: 'success',
      duration: 0
    })
    //@ts-ignore
    this.$message.close()
    const formData = new FormData()
    if (file.status === 'ready') {
      formData.append('file', file.raw)
      formData.append('name', file.name)
      formData.append('type', 'PRODUCT')
      this.productApi
        .uploadFile(formData)
        .then(() => {
          message.close()
          this.$message.success('Tải lên thành công')
          this.init()
        })
        .catch(error => {
          if (error.data.length) {
            message.close()
            this.dataError = error.data
            this.fileName = file.name
            this.setOpenPopup({
              popupName: 'popup-show-error',
              isOpen: true
            })
          }
          message.close()
          if (error.errorCode === 429) {
            this.$message.error('Không upload được file khi hệ thống đang cập nhật dữ liệu. Vui lòng thử lại sau ít phút')
          } else {
            this.$message.error('Tải lên thất bại')
          }
        })
    }
    this.isSpace = false
  }
  handleSearch(value: string): void {
    this.query.page = 1
    this.query.search = value
    this.init()
  }
  handleCommand(command: Record<string, any>): void {
    forEach(this.option, item => {
      if (command.id === item.id) {
        item.active = true
      } else {
        item.active = false
      }
    })
    this.query.sortType = command.sortType
    this.init()
  }
  created(): void {
    this.form = this.user
    this.productApi = getRepository('product', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiImport = getRepository('import', {
      districtId: this.user?.districts?.[0].id
    })
    this.init()
  }
}
